import { Scrollbars } from "react-custom-scrollbars";
import { Grid } from "@material-ui/core";
import React, {useEffect, useState} from "react";
import KSService from "../../services/KSService";
import { BannerPrompt } from "../start-page-v2/components/cards/BannerPrompt";
import TestSuiteAnalyticsPlaceholder from "./TestSuiteAnalyticsPlaceholder";
import { TestSuiteSummary } from "./TestSuiteSummary";
import { makeStyles } from "@material-ui/styles";
import { ErrorCardComponent } from "../start-page-v2/components/cards/ErrorCardComponent";

const styles = makeStyles({
  root: {
    minWidth: "700px",
    padding: '14px 17px',
    height: '100%',
    borderRadius: '4px',
    border: 'solid 1px #d6dae4',
    backgroundColor: '#fff',
    fontFamily: "Inter, Roboto, Helvetica, Arial, sans-serif",
  },
});

export default function TestSuiteAnalytics() {
  
  const classes = styles();
  document.body.style.background = "white";

  const [projectConfiguration, setProjectConfiguration] = useState(null);

  const [isRenderErrorComponent, setIsRenderErrorComponent] = useState(false);

  const fetchProjectConfiguration = async () => {
    const data = await KSService.fetchProjectConfiguration();
    setProjectConfiguration(data);
    responseErrorChecking(data, () => {
      setProjectConfiguration(data);
    });
  };

  const responseErrorChecking = (data, callback) => {
    if (data?.hasError) {
      setIsRenderErrorComponent(true);
    } else {
      callback();
    }
  }

  useEffect(() => {
    fetchProjectConfiguration();
  }, []);

  const handleRefresh = async () => {
    setProjectConfiguration(null);
    fetchProjectConfiguration();
  };

  const testOpsServerUrl = projectConfiguration?.testOpsServerUrl;

  const projectLink = `${testOpsServerUrl}/team/${projectConfiguration?.testOpsProject?.teamId}/project/${projectConfiguration?.testOpsProject?.id}`;

  const renderBody = () => {
    const { isIntegrationEnabled } = projectConfiguration;

    return (
      <Grid container className={classes.root}>
        {isIntegrationEnabled ? (
          <TestSuiteSummary
            testOpsServerUrl={testOpsServerUrl}
            handleRefresh={handleRefresh}
            projectLink={projectLink}
            responseErrorChecking={responseErrorChecking}
          />
        ) : (
          <BannerPrompt />
        )}
      </Grid>
    );
  };

  return (
    <>
      {isRenderErrorComponent ? (
        <ErrorCardComponent isRenderPlatformTitle={false} />
      ) : (
        <Scrollbars
          autoHide
          hideTracksWhenNotNeeded
          style={{ width: "100%" }}
          autoHeightMin="100%"
          autoHeightMax="100%"
        >
          <Grid container>
            {projectConfiguration ? (
              renderBody()
            ) : (
              <TestSuiteAnalyticsPlaceholder />
            )}
          </Grid>
        </Scrollbars>
      )}
    </>
  );
}
