import { Button, Grid, makeStyles } from "@material-ui/core";
import React from "react";
import {
  IconBooks,
  IconChartSimple,
  IconLongArrowRight,
} from "../../../../images/CustomIcon";
import PlatformBannerPromptImg from "../../../../../assets/img/platform-banner-prompt.svg";
import { Link } from "../../../../utils/Constants";
import KSActionService from "../../../../services/KSActionService";

const styles = makeStyles({
  root: {
    padding: "14px 17px",
    height: "217px",
    borderRadius: "4px",
    border: "solid 1px #d6dae4",
    backgroundColor: "#fff",
  },
  iconChart: {
    marginRight: "6px",
  },
  title: {
    fontSize: "10px",
    fontWeight: 600,
    color: "#233145",
    marginBottom: "6px",
    display: "flex",
    alignItems: "center",
  },
  content: {
    fontSize: "12px",
    color: "#233145",
    marginBottom: "12px",
  },
  goToPlatformBtn: {
    fontWeight: "400",
    fontSize: "12px",
    textTransform: "none",
    color: "#fff",
    backgroundColor: "#22283c",
    "&:hover": {
      color: "#fff",
      backgroundColor: "#22283c",
    },
  },
  iconArrow: {
    marginLeft: "5px",
  },
  iconLearnMore: {
    marginRight: "5px",
  },
  learnMoreBtn: {
    padding: 0,
    marginTop: "8px",
    fontWeight: 400,
    fontSize: "12px",
    textTransform: "none",
    color: "#233145",
    "&:hover": {
      backgroundColor: "transparent",
    },
  },
  media: {
    textAlign: "right",
  },
});

export const BannerPrompt = () => {
  const classes = styles();

  const openLink = (link) => {
    window.location.href = link;
  };

  function performConfigureIntegration(link) {
    KSActionService.openIntegrateWithPlatform();
  }

  return (
    <>
      <Grid item xs={5}>
        <div className={classes.title}>
          <IconChartSimple className={classes.iconChart} />
          <span>PLATFORM ANALYTICS</span>
        </div>
        <div className={classes.content}>
          We provide insights that value for your teamâ€™s workflow and identify
          areas for improvement on Katalon Platform.
        </div>
        <div>
          <Button
            variant="contained"
            className={classes.goToPlatformBtn}
            onClick={() =>
              performConfigureIntegration(Link.startedWithTheKatalonPlatform)
            }
          >
            Configure Cloud Platform Integration
            <IconLongArrowRight className={classes.iconArrow} />
          </Button>
        </div>
        <div>
          <Button
            variant="text"
            className={classes.learnMoreBtn}
            onClick={() => openLink(Link.introductionToTestAnalysis)}
          >
            <IconBooks className={classes.iconLearnMore} />
            <span> Learn more </span>
          </Button>
        </div>
      </Grid>
      <Grid item xs={7} className={classes.media}>
        <img src={PlatformBannerPromptImg} alt="illustration" />
      </Grid>
    </>
  );
};
