import { Button, Divider, Grid, makeStyles } from "@material-ui/core";
import { useState, useEffect } from "react";
import { isArray, get } from 'lodash';
import { IconBestPractices,
  IconChartSimple,
  IconChevronRight,
  IconEmptySet,
  IconExternalLink,
  IconExternalLinkGray,
  IconRefresh } from "../../images/CustomIcon";
import { Link } from "../../utils/Constants";
import ExecutionDecorator from "../../utils/decorator/ExecutionDecorator";
import PlatformTestSuiteImg from '../../../assets/img/platform-test-suite-img.svg';
import PlatformScheduleImg from '../../../assets/img/platform-schedule-img.svg';
import KSService from "../../services/KSService";
import { AnalyticsPlaceholder } from "../start-page-v2/components/cards/AnalyticsPlaceholder";

const styles = makeStyles({
  dashedBorder: {
    borderStyle: "dashed",
    color: "#dbdde5",
    margin: "0",
  },
  iconChart: {
    marginRight: '6px',
  },
  title: {
    fontSize: '10px',
    fontWeight: 600,
    color: '#233145',
    marginBottom: '6px',
    display: 'flex',
    alignItems: 'center',
  },
  idSection: {
    display: "flex",
    fontSize: "14px",
    fontWeight: "500",
    color: "#233145",
    marginBottom: "8px",
  },
  detailSection: {
    marginTop: "2px",
    display: "flex",
    fontSize: "12px",
    color: "#808b9a",
    alignItems: "center",
  },
  testRunRow: {
    display: "flex",
    alignItems: "center",
    cursor: "pointer",
    padding: "7px 2px",
  },
  testRunHover: {
    '&:hover': {
      backgroundColor: "#F5F5F5",
      borderRadius: "4px",
    },
  },
  actionBtn: {
    alignItems: "center",
    fontSize: '12px',
    fontWeight: '400',
    textTransform: "none",
    color: "#808b9a",
  },
  iconExternalLink: {
    marginLeft: '5px',
  },
  learnMoreBtn: {
    justifyContent: "flex-start",
    textTransform: "none",
    fontWeight: "normal",
    fontSize: "12px",
    '&:hover': {
      backgroundColor: "transparent",
    },
  },
  lineDivider: {
    width: "0px",
    border: "1px dashed #dbdde5",
    backgroundColor: "transparent",
  },
  sectionDescription: {
    fontSize: "12px",
    marginLeft: "4px",
    color: "#4d5369",
    width: "195px",
  },
  iconEmptySet: {
    marginRight: "12px",
  },
  alignContent: {
    width: "100%",
    textAlign: "center",
  },
  emptyTest: {
    fontSize: "11px",
    color: "#727993",
    paddingBottom: "13px",
  },
  actionSection: {
    display: "flex",
    justifyContent: "flex-end",
    height: "32px",
  },
  iconRefresh: {
    marginRight: '4px',
  },
  goToPlatformBtn: {
    fontWeight: '400',
    marginLeft: "5px",
    textTransform: "none",
    color: "#fff",
    backgroundColor: "#22283c",
    '&:hover': {
      color: "#fff",
      backgroundColor: "#22283c",
    },
  },
  refreshBtn: {
    fontWeight: '400',
    textTransform: "none",
    color: "#808b9a",
  },
  headerSectionContent: {
    fontSize: '12px',
    color: '#233145',
    marginBottom: '12px',
  },
  latestExecutions: {
    width: "100%"
  },
  latestExecutionsTitle: {
    fontSize: "12px",
    fontWeight: "bold",
    color: "#808b9a",
  },
  infoSection: {
    display: "flex",
  },
  barSection: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
  },
  iconChevron: {
    marginLeft: "18px",
    marginRight: "10px",
  },
  summarySection: {
    minWidth: "200px",
    width: "fit-content",
    padding: "12px 16px",
    borderRadius: "4px",
    backgroundColor: "rgba(34, 40, 60, 0.04)",
    marginBottom: "16px",
  },
  summaryTitle: {
    fontSize: '12px',
    marginBottom: '6px',
    fontWeight: '400',
    color: '#808b9a',
  },
  summaryValue: {
    fontSize: '18px',
    fontWeight: '600',
    color: '#233145',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    "& div": {
      paddingRight: "8px",
      minWidth: "145px",
    }
  },
  bestPracticesIcon: {
    marginRight: "-4px"
  },
});

export const TestSuiteSummary = ({testOpsServerUrl, projectLink, handleRefresh, responseErrorChecking}) => {
  const classes = styles();

  const [latestExecutions, setLatestExecutions] = useState(null);

  const testRunLink = `${projectLink}/executions`;

  const testRunDetailLink = (executionId) => `${projectLink}/executions/${executionId}`;

  const projectExecutionLink = `${projectLink}/test-planning?isOpenScheduleTestRunDialog=true`;

  const openLink = (link) => {
    window.location.href = link;
  }

  const fetchLatestExecutions = async (testOpsServerUrl) => {
    const url = new URLSearchParams(window.location.search);
    const testSuitePath = url.get('testSuitePath');
    const testSuiteName = url.get('testSuiteName');
    const data = await KSService.fetch6LatestExecutionsTestSuite(testSuitePath, testSuiteName, testOpsServerUrl);
    responseErrorChecking(data, () => {
      if (!isArray(data)) {
        setLatestExecutions([]);
      } else {
        setLatestExecutions(data)
      }
    });
  };

  useEffect(() => {
    if (testOpsServerUrl) {
      fetchLatestExecutions(testOpsServerUrl);
    }
  }, []);

  const handleSelectRow = (row) => {
    openLink(testRunDetailLink(row.order))
  };

  const HeaderSection = () => {
    return (
      <Grid container>
        <Grid xs={7}>
        <div className={classes.title}>
          <IconChartSimple className={classes.iconChart} />
          <span>
            PLATFORM ANALYTICS
          </span>
        </div>
        <div className={classes.headerSectionContent}>
          We provide insights that value for your teamâ€™s workflow and identify areas for improvement on Katalon Platform.
          <Button
            className={classes.learnMoreBtn}
            size="small"
            startIcon={<IconBestPractices className={classes.bestPracticesIcon}/>}
            href={Link.introductionToTestAnalysis}
          >
            Learn more
          </Button>
        </div>
        </Grid>
        <Grid xs={5} className={classes.actionSection}>
          <Button variant="text" className={classes.refreshBtn} onClick={handleRefresh}>
            <IconRefresh className={classes.iconRefresh}/>
            <span> Refresh </span>
          </Button>
          <Button evelation={0} variant="contained" color="success" className={classes.goToPlatformBtn} onClick={() => openLink(projectLink)}>
            Analyze on Platform
            <IconExternalLink className={classes.iconExternalLink}/>
          </Button>
        </Grid>
      </Grid>
    );
  };

  const EmptyState = () => {
    return (
      <>
        <HeaderSection />
        <div className={classes.alignContent}>
          <div><IconEmptySet className={classes.iconEmptySet}/></div>
          <div className={classes.emptyTest}>
            There is no data to display. Things you can do are:
          </div>
        </div>
        <Grid container justifyContent="center">
          <div>
            <img src={PlatformTestSuiteImg} alt="illustration" />
            <div className={classes.sectionDescription}>Run a Test Suite in Katalon Studio.</div>
            <Button
              className={classes.learnMoreBtn}
              size="small"
              startIcon={<IconBestPractices className={classes.bestPracticesIcon} />}
              href={Link.executeTestSuiteRootDoc}
            >
              Learn more
            </Button>
          </div>
          <Divider orientation="vertical" className={classes.lineDivider} variant="middle" flexItem />
          <div>
            <img src={PlatformScheduleImg} alt="illustration" />
            <div className={classes.sectionDescription}>Schedule a Test Run with this Test Suite on Katalon Platform.</div>
            <Button
              className={classes.learnMoreBtn}
              size="small"
              startIcon={<IconBestPractices className={classes.bestPracticesIcon} />}
              href={Link.scheduleTestRunInTestOpsDoc}
            >
              Learn more
            </Button>
          </div>
        </Grid>
      </>
    );
  }

  if (!latestExecutions) {
    return <AnalyticsPlaceholder />
  }

  if (latestExecutions.length < 1) {
    return <EmptyState />
  }

  const calculateAverageDurationOfExecutions = (executions) => {
    if (executions.length === 0) {
      return 0;
    }
    const totalDuration = executions.reduce((total, item) => total + get(item, 'elapsedDuration'), 0);
    return totalDuration / executions.length;
  }

  const SummarySection = () => {
    const last3Runs = latestExecutions.slice(0, 3);
    const last3ExecutionAvgDuration = calculateAverageDurationOfExecutions(last3Runs);

    const renderTrendDecorator = () => {
      if (latestExecutions.length === 6) {
        const previous3Runs = latestExecutions.slice(3, 6);
        const previous3ExecutionAvgDuration = calculateAverageDurationOfExecutions(previous3Runs);
        if (previous3ExecutionAvgDuration === 0 || isNaN(previous3ExecutionAvgDuration)) {
          return null;
        }
        const ratio = (last3ExecutionAvgDuration - previous3ExecutionAvgDuration) / previous3ExecutionAvgDuration * 100;
        return ExecutionDecorator.trendDecoratorV2(ratio);
      } else {
        return null;
      }
    }

    return (
      <Grid container>
        <Grid className={classes.summarySection}>
          <div className={classes.summaryTitle}>Avg. Duration of Last 3 Runs</div>
          <div className={classes.summaryValue}>
            <div>
              {ExecutionDecorator.durationFormatDecorator(last3ExecutionAvgDuration)}
            </div>
            {renderTrendDecorator()}
          </div>
        </Grid>
      </Grid>
    );
  };

  const LatestExecutions = () => {
    return (
      <div className={classes.latestExecutions}>
        {latestExecutions.slice(0, 3).map((execution, index) => (
          <Grid className={classes.testRunHover} onClick={() => handleSelectRow(execution)}>
            <Grid className={classes.testRunRow}>
              <Grid xs={8} className={classes.infoSection}>
                {ExecutionDecorator.lineStatusDecorator(execution)}
                <div>
                  <Grid item xs={12} className={classes.idSection}>
                    {ExecutionDecorator.statusDecorator(execution)}
                    {ExecutionDecorator.nameDecorator(execution)}
                  </Grid>
                  <Grid item xs={12} className={classes.detailSection}>
                    {ExecutionDecorator.idDecorator(execution)}
                    {ExecutionDecorator.startTimeDecorator(execution)}
                    {ExecutionDecorator.durationDecorator(execution)}
                    {ExecutionDecorator.oSDecorator(execution)}
                    {ExecutionDecorator.browserDecorator(execution)}
                    {ExecutionDecorator.profileDecorator(execution)}
                    {ExecutionDecorator.asigneeDecorator(execution)}
                  </Grid>
                </div>
              </Grid>
              <Grid xs={4} className={classes.barSection}>
                {ExecutionDecorator.statusBarTestRunDecorator(execution)}
                <IconChevronRight className={classes.iconChevron} />
              </Grid>
            </Grid>
            {index + 1 !== latestExecutions.length && <hr className={classes.dashedBorder} />}
          </Grid>
        ))}
        {latestExecutions.length < 3 &&
          <>
            <hr className={classes.dashedBorder} style={{ marginBottom: "7px" }}/>
              <div className={classes.alignContent}>
                  <div><IconEmptySet className={classes.iconEmptySet}/></div>
                  <div className={classes.emptyTest}>
                    There's no more run to display
                  </div>
                </div>
            <hr className={classes.dashedBorder} />
          </>
        }
        <Button variant="text" className={classes.actionBtn} onClick={() => openLink(testRunLink)}>
          View All 
          <IconExternalLinkGray className={classes.iconExternalLink}/>
        </Button>
        <Button variant="text" className={classes.actionBtn} onClick={() => openLink(projectExecutionLink)}>
          Schedule Test
          <IconExternalLinkGray className={classes.iconExternalLink}/>
        </Button>
      </div>
    );
  };

  return (
    <>
      <HeaderSection />
      <SummarySection />
      <span className={classes.latestExecutionsTitle}>
        LAST 3 RUNS
      </span>
      <LatestExecutions />
    </>
  );
}
